import Chart from "chart.js";

const chartHelper = {
  createBarChart: (chartElement, datasets, labels, title = null) => {
    const chart = new Chart(chartElement, {
      type: 'bar',
      data: {
        datasets: datasets,
        labels: labels
      },
      options: chartHelper.createOptions(title)
    });

    return chart;
  },
  createDataSet: (label, data, backgroundColor) => {
    return {
      label: label,
      data: data,
      backgroundColor: backgroundColor
    }
  },
  createOptions: (title = null) => {
    const titleOption = (title) ? { display: true, text: title } : { display: false };

    return {
      title: titleOption,
      responsive: true,
      lineTension: 1,
      scales: {
        yAxes: [{
          ticks: {
            beginAtZero: true,
          }
        }]
      }
    };
  }
};

export default chartHelper;



